
.ir {
  position: relative;
  height: 0;
  overflow: hidden;
}
.container {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
.ir169 {
  composes: ir;
  padding-bottom: calc(100% / (16 / 9));
}
.ir32 {
  composes: ir;
  padding-bottom: calc(100% / (3 / 2));
}
.ir43 {
  composes: ir;
  padding-bottom: calc(100% / (4 / 3));
}
.ir11 {
  composes: ir;
  padding-bottom: calc(100% / (1 / 1));
}
.ir34 {
  composes: ir;
  padding-bottom: calc(100% / (3 / 4));
}
.ir23 {
  composes: ir;
  padding-bottom: calc(100% / (2 / 3));
}



