
h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
  font-weight: normal;
}
p, figure, blockquote, ul, ol, li {
  margin: 0;
  padding: 0;
}
a img {
  line-height: 0;
  display: block;
}
figure a {
  display: block;
}
button,
input[type="submit"] {
  border: none;
  margin: 0;
  padding: 0;
  background-color: transparent;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}



