
@value lem from './../framework/scale.css';
.link {
  text-decoration: none;
  color: gray;
}
.about {
  background: -webkit-gradient(linear, left top, right bottom, from(white), to(black));
  background: linear-gradient(to right bottom, white, black);
  color: lightgray;
  will-change: color;
  -webkit-transition: color 1s ease-in-out;
  transition: color 1s ease-in-out;
}
.about:hover {
  -webkit-animation: pulse 10s ease-in-out infinite;
          animation: pulse 10s ease-in-out infinite;
}
.text {
  padding: lem;
  /* This is fully responsive and readable on all screens */
  font-size: 6vmin;
  text-align: justify;
}
@-webkit-keyframes pulse {
25% {
    color: gray;
    opacity: .66;
}
50% {
    color: red;
    opacity: .33;
}
75% {
    color: yellow;
    opacity: .10;
}
100% {
    color: lightgray;
    opacity: 1;
}
}
@keyframes pulse {
25% {
    color: gray;
    opacity: .66;
}
50% {
    color: red;
    opacity: .33;
}
75% {
    color: yellow;
    opacity: .10;
}
100% {
    color: lightgray;
    opacity: 1;
}
}



