
.flip {
  position: relative;
  display: inline-block;
  outline: none;
  overflow: hidden;
  cursor: pointer;
}
.flip__span {
    position: relative;
    display: inline-block;
    will-change: transform;
    -webkit-transition: -webkit-transform 0.3s cubic-bezier(0.465, 0.183, 0.153, 0.946);
    transition: -webkit-transform 0.3s cubic-bezier(0.465, 0.183, 0.153, 0.946);
    transition: transform 0.3s cubic-bezier(0.465, 0.183, 0.153, 0.946);
    transition: transform 0.3s cubic-bezier(0.465, 0.183, 0.153, 0.946), -webkit-transform 0.3s cubic-bezier(0.465, 0.183, 0.153, 0.946);
}
.flip__span::before {
      position: absolute;
      top: 100%;
      content: attr(data-hover);
      -webkit-transform: translate3d(0, 0, 0);
              transform: translate3d(0, 0, 0);
}
.flip:hover, .flip:focus, .flip:active {
    outline: none;
}
.flip:hover .flip__span, .flip:focus .flip__span, .flip:active .flip__span {
      -webkit-transform: translateY(-100%);
              transform: translateY(-100%);
}



